import React, { Suspense } from "react";
import ReactDOM from "react-dom/client";
import "./App.scss";
import App from "./App";
import { Provider } from "react-redux";
import { createStore } from "./app/store/store";
import { persistor } from "./app/store/store";
import { PersistGate } from "redux-persist/integration/react";
import LanguageDetector from "i18next-browser-languagedetector";
import i18n from "i18next";
import { initReactI18next } from "react-i18next";
import HttpApi from "i18next-http-backend";
import enJson from "./app/assets/locales/en/translation.json";
import ruJson from "./app/assets/locales/ru/translation.json";
import uaJson from "./app/assets/locales/ua/translation.json";

i18n
  .use(initReactI18next)
  .use(LanguageDetector)
  .use(HttpApi)
  .init({
    resources: {
      ru: { translation: ruJson },
      en: { translation: enJson },
      ua: { translation: uaJson },
    },
    fallbackLng: "ru",
    detection: {
      order: ["coockie", "localStorage", "htmlTag", "path", "subdomain"],
      caches: ["coockie"],
    },
    backend: {
      loadPath: "./app/assets/locales/{{lng}}/translations.json",
    },
  });
const root = ReactDOM.createRoot(
  document.getElementById("root") as HTMLElement
);
const GatePersist: any = PersistGate;
const store = createStore();

root.render(
  // <Suspense>
  <React.StrictMode>
    <Provider store={store}>
      <GatePersist loading={null} persistor={persistor}>
        <App />
      </GatePersist>
    </Provider>
  </React.StrictMode>
  // </Suspense>
);
